import OrderTypeAndDeliveryDate from "components/orderTypeAndDeliveryDate/OrderTypeAndOrderDate";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";

import SwitchItem from "components/formItems/switch/SwitchItem";
import PackCard from "components/packCard/PackCard";
import ProductList from "components/produstList/ProductList";
import SearchBox from "components/searchBox/SearchBox";
import { useLocation, useNavigate } from "react-router-dom";
// nuts start
import almonds from "assets/images/temporaryImages/nuts/tinyjpg/almonds.jpg";
import cashew from "assets/images/temporaryImages/nuts/tinyjpg/cashew.jpg";
import hazelnut from "assets/images/temporaryImages/nuts/tinyjpg/hazelnut.jpg";
import macadamia from "assets/images/temporaryImages/nuts/tinyjpg/macadamia.jpg";
import SelectChild from "components/selectChild/SelectChild";
import TabForSelectOrderType from "components/tabToSelect/TabForSelectOrderType";
import { useCartContext } from "context/CartContext";
import { useTemporaryCartContext } from "context/TemporaryCartContext";
import { checkSchoolTemporaryCartAndMoveToCart } from "functions/functions";
import AddChildComponent from "components/children/AddChildComponent";

// end of nuts
const SchoolOrder = () => {
  const [showAddChildForm, setShowAddChildForm] = useState(false);
  const [children, setChildren] = useState([]);
  const packData = [
    { nutsName: "pack 1", nutsImage: almonds, packPrice: 1, cal: 280 },
    { nutsName: "pack 2", nutsImage: cashew, packPrice: 2, cal: 360 },
    { nutsName: "pack 3", nutsImage: hazelnut, packPrice: 3, cal: 430 },
    { nutsName: "pack 4", nutsImage: macadamia, packPrice: 4, cal: 570 },
  ];
  const defaultChildrenList = [
    {
      id: 0,
      name: "Olivia",
      FTEE: 400,
    },
    {
      id: 1,
      name: "Noah",
      FTEE: 330,
    },
    {
      id: 2,
      name: "Emma",
      FTEE: 280,
    },
    {
      id: 3,
      name: "Liam",
      FTEE: 370,
    },
  ];

  const getAllchildList = () => {
    if (localStorage.getItem("test-child-list")) {
      try {
        const childs = localStorage.getItem("test-child-list");
        const parsedChild = JSON.parse(childs);
        setChildren(parsedChild);
      } catch {
        setChildren(defaultChildrenList);
      }
    } else {
      setChildren(defaultChildrenList);
    }
  };

  useEffect(() => {
    getAllchildList();
  }, [showAddChildForm]);

  const navigate = useNavigate();
  const { t } = useTranslation();

  const location = useLocation();

  const queryParams = new URLSearchParams(location.search);
  // get from querString url
  const defaultDeliveryDate = queryParams.get("date");

  // const [deliveryDateAndTime, setDeliveryDateAndTime] = useState(null);
  const [itemTypes, setItemTypes] = useState(null);
  const [selectedChild, setSelectedChild] = useState(null);
  const [searchText, setSearchText] = useState(null);
  const [examCheckBox, setExamCheckBox] = useState(false);
  const [sportCheckBox, setSportCheckBox] = useState(false);
  const [noBreakfastCheckBox, setNoBreakfastCheckBox] = useState(false);
  const { temporaryCart, setTemporaryCart } = useTemporaryCartContext();
  const { cart, setCart } = useCartContext();
  const [deliveryDate, setDeliveryDate] = useState(defaultDeliveryDate || null);
  const [selectedChildFTEE, setSelectedChildFTEE] = useState(null);

  useEffect(() => {
    setItemTypes("custom");
    // if (deliveryDateAndTime) {
    //   setDeliveryDate(deliveryDateAndTime?.slice(0, 10));
    // }
  }, [deliveryDate]);

  useEffect(() => {
    console.log(selectedChild);
  }, [selectedChild]);

  // reset temporary cart on load
  useEffect(() => {
    setTemporaryCart([]);
  }, []);

  const FteeValueHandler = () => {
    try {
      if (selectedChild) {
        let initialFTEE = selectedChild.FTEE;

        if (examCheckBox) {
          initialFTEE += 150;
        }
        if (sportCheckBox) {
          initialFTEE += 498;
        }
        if (noBreakfastCheckBox) {
          initialFTEE += 360;
        }
        selectedChild.needFTEE = initialFTEE;
        setSelectedChildFTEE(initialFTEE);
      }
    } catch {
      setSelectedChildFTEE(selectedChild.FTEE);
    }
  };

  useEffect(() => {
    FteeValueHandler();
  }, [selectedChild, examCheckBox, sportCheckBox, noBreakfastCheckBox]);

  // useEffect(() => {
  // if (haveOrderTypeSchoolInSelectedDate(cart, deliveryDate, itemTypes)) {
  //   // console.log("yes we have some order");
  //   // console.log(haveOrderTypeSchoolInSelectedDate(cart, deliveryDate, itemTypes))
  //   const childHaveOrderInThisDate = haveOrderTypeSchoolInSelectedDate(
  //     cart,
  //     deliveryDate,
  //     itemTypes
  //   ).some((item) => item?.child?.id === selectedChild?.id);
  //   if (childHaveOrderInThisDate) {
  //     console.log("childHaveOrderInThisDate");
  //     setSelectedChild(null);
  //     return notification(
  //       "duplicate child",
  //       "child exist this date",
  //       "warning"
  //     );
  //     // }
  //   }
  // }
  // }, [deliveryDate, selectedChild]);

  const checkTemporaryCartToContinue = () => {
    if (
      checkSchoolTemporaryCartAndMoveToCart(
        temporaryCart,
        setTemporaryCart,
        selectedChild,
        deliveryDate,
        cart,
        setCart
      )
    ) {
      navigate("/cart");
    }
  };
  return (
    <div className="w-full flex flex-col">
      <OrderTypeAndDeliveryDate
        deliveryDate={deliveryDate}
        setDeliveryDate={setDeliveryDate}
        orderType={t("school")}
      />
      {deliveryDate === null ? null : (
        <>
          <div className="flex flex-col mt-3">
            <SelectChild
              children={children}
              setSelectedChild={setSelectedChild}
              selectedChild={selectedChild}
              setShowAddChildForm={setShowAddChildForm}
            />
            {selectedChildFTEE && (
              <div className="flex flex-col text-xs px-2 my-3 py-1 bg-[#87CB44] rounded-md">
                {/* <div className="flex flex-row">
          <span>{t("totalValue")}</span>:
          <span className="mx-1">{totalCalories}</span>
        </div> */}
                <div className="flex flex-row">
                  <span>{t("dailyValue")}</span>:
                  <span className="mx-1">{selectedChildFTEE}</span>
                </div>
              </div>
            )}
            {selectedChild ? (
              <div className="w-full h-auto px-3 py-2 rounded-[10px] box-shadow-primary bg-[#fff] mt-2">
                <p className="text-[#717171] mb-3 ">{t("childsStatus")}</p>
                <SwitchItem
                  switchStatus={examCheckBox}
                  toggleSwitchHandler={setExamCheckBox}
                  name={t("exam")}
                />
                <SwitchItem
                  switchStatus={sportCheckBox}
                  toggleSwitchHandler={setSportCheckBox}
                  name={t("sport")}
                />
                <SwitchItem
                  switchStatus={noBreakfastCheckBox}
                  toggleSwitchHandler={setNoBreakfastCheckBox}
                  name={t("no breakfast")}
                />
              </div>
            ) : null}
          </div>
          {/* 
          <div className="all-center">
            <OptionsItem
              setSelectedChild={setSelectedChild}
              selectedChild={selectedChild}
            />
          </div> */}
          {selectedChild ? (
            <>
              <div className="w-full flex flex-row justify-evenly mt-5">
                {/* <TabForSelect
              itemTypes={itemTypes}
              setItemTypes={setItemTypes}
              firstTab={t("Ferezi choice")}
              secondTab={t("Your choice")}
            /> */}
                <TabForSelectOrderType
                  itemTypes={itemTypes}
                  setItemTypes={setItemTypes}
                  firstTab={t("Ferezi choice")}
                  secondTab={t("Your choice")}
                  className=""
                />
              </div>

              <div className="w-full h-full mt-5 flex flex-col gap-3">
                {itemTypes === "pack" ? (
                  <>
                    {packData.map((pack, index) => {
                      return (
                        <PackCard
                          price={pack.packPrice}
                          nutsImage={pack.nutsImage}
                          nutsName={pack.nutsName}
                          id={index + 100}
                          orderTypeIsSchool={true}
                          cal={
                            selectedChildFTEE
                              ? Math.floor(
                                  (selectedChildFTEE + (index * 17 - 27)) / 10
                                ) * 10
                              : " "
                          }
                        />
                      );
                    })}
                  </>
                ) : (
                  <>
                    <SearchBox searchText={searchText} />
                    <ProductList orderType="school" />
                  </>
                )}
              </div>
              <div className="w-full all-center my-5">
                <button
                  onClick={() => {
                    checkTemporaryCartToContinue();
                  }}
                  className="fr-primary-button block"
                >
                  {t("continue")}
                </button>
              </div>
            </>
          ) : null}
        </>
      )}
      {showAddChildForm && (
        <AddChildComponent
          showBottomSheet={showAddChildForm}
          setShowBottomSheet={setShowAddChildForm}
        />
      )}
    </div>
  );
};

export default SchoolOrder;
